<template>
  <el-form
    class="ling-ffc-calibration-switch"
    label-position="right"
    label-width="100px"
    :model="calibrationSwitch"
    v-loading="loading"
    ref="switchForm">
    <p v-if="isFirstType" key="1" class="tips">注意！！！使用该功能是，必须将温度摄像头前方使用均匀恒温物体遮挡，才可以校准。校准后，会让温度摄像头补偿掉噪点提高准确性。</p>
    <p v-else key="2" class="tips">注意！！！该功能只能维护人员操作，使用该功能，必须将黑体完全遮挡温度摄像头，并填入黑体的温度值，再进行校准。提高绝对温度准确性。</p>
    <el-form-item label="校准类型：">
      <el-radio-group v-model="switchType">
        <el-radio :label="1">平均温度校准</el-radio>
        <el-radio :label="2">黑体温度校准</el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="校准参数：" v-if="!isFirstType">
      <el-input type="number" :min="0" v-model="calibrationSwitch.param" placeholder="请输入FFC黑体温度校准参数"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="handleSave('switchForm')">校准生效</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import getSettingInfo from '@/mixins/getSettingInfo'
import { mapActions } from 'vuex'
export default {
  name: 'LingFFCCalibrationSwitch',
  mixins: [getSettingInfo],
  data () {
    return {
      loading: false,
      calibrationSwitch: {
        type: 1,
        param: 0
      }
    }
  },
  computed: {
    switchType: {
      get () {
        return this.calibrationSwitch.type
      },
      set (val) {
        this.calibrationSwitch.type = val

        if (this.isFirstType) {
          this.calibrationSwitch.param = 0
        }
      }
    },
    isFirstType () {
      return this.calibrationSwitch.type === 1
    }
  },
  watch: {
    getSettingInfo: {
      handler (val) {
        this.calibrationSwitch.param = +val.ffc_calibration_parameter
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    ...mapActions({
      setFFCBlackCalibrationn: 'setFFCBlackCalibrationn',
      setFFCAvgCalibrationn: 'setFFCAvgCalibrationn'
    }),
    handleSave (formName) {
      if (this.loading) {
        return
      }

      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.loading = true
          if (this.calibrationSwitch.type === 1) {
            try {
              await this.setFFCAvgCalibrationn()
            } catch (res) {}
          } else {
            try {
              await this.setFFCBlackCalibrationn({
                ffc_calibration_parameter: this.calibrationSwitch.param
              })
            } catch (res) {}
          }

          this.loading = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
.ling-ffc-calibration-switch {
  .el-radio + .el-radio {
    margin: 10px 0;
  }
}
</style>



